button{
    background: #4782D3;
    padding: 1rem 2rem;
    border-radius: 40px;
    color: #fff;
    text-transform: uppercase;
    outline: none;
    cursor: pointer;
    border: 1px solid #4782D3;
    font-size: 16px;
    font-weight: 500;
}
button:hover{
    background: #ffffff00;
    border: 1px solid #4782D3;
}

.banner{
    background-image: 
    linear-gradient(
      rgba(0, 0, 0, 0.7), 
      rgba(0, 0, 0, 0.7)
    ),
    url(../../Image/banner.jpg);
    height: 400px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
}
.banner_text{
    text-align: center;
    color: #fff;
}
.banner_text h2{
    font-size: 55px;
    font-weight: 700;
}
.banner_text p{
    font-size: 18px;
    margin: 1.5rem 0;
    line-height: 28px;
    font-weight: 300;
}
