a{
    text-decoration: none;
}
.flex{
    display: flex;
}
.card{
    padding: 1rem 3rem;
    text-align: center;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.card h3{
    font-weight: 500;
    margin-top: 1rem;
}
.card p{
    margin: 1rem 0;
    font-size: 15px;
    font-weight: 300;
    color: #7a7a7a;
    line-height: 25px;
}
.icon{
    width: 70px;
    height: 70px;
    align-items: center;
    justify-content: center;
    margin: 2rem 0;
    border-radius: 50%;
    background-color: #4782D3;
}
.icon img{
    width: 25px;
}
.card a{
    color: #4782D3;
    font-size: 15px;
}