footer{
    background-color: #000;
    padding: 2rem 3rem;
}
footer img{
    width: 100px;
    height: 100px;
}
.content_wrapper{
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    padding: 1rem 2rem;
}
.content_wrapper h2{
    color: #fff;
    margin: 1.5rem 0;
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 500;
}
.post{
    padding-left:5rem;
}
.post div{
    margin: 1rem 0;
}
.post p{
    margin: .4rem 0;
}
.content_wrapper h5{
    color: #a3a3a3;
    font-size: 18px;
    font-weight: 500;
}
.content_wrapper p{
    color: #a3a3a3;
    line-height: 23px;
    width: 300px;
    font-size: 15px;
}
.gallery{
    padding-left: 4.5rem;
}
.gallery img{
    margin: .2rem;
}